container {
    display:inline-block;
}

.card{
    background: #fff;
    border-radius: 4px;
    height: 145px;
    margin: 1rem;
    position: relative;
    width: 170px;
    -webkit-box-shadow: 0 3px 6px #999, 0 3px 6px #999;
            box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    text-align:center;
    -webkit-transition: all .2s ease-in-out;
    -o-transition: all .2s ease-in-out;
    transition: all .2s ease-in-out;
}

.img-responsive {
    width: 80%;
    margin-left: 20px;
    padding-top:8px;
}

.card:hover {
    -webkit-box-shadow: 10px 10px 20px rgb(35, 216, 216);
            box-shadow: 10px 10px 20px rgb(35, 216, 216);
    -webkit-transform: scale(1.1);
        -ms-transform: scale(1.1);
            transform: scale(1.1);
    cursor: pointer;
  }


